.Example {
    display: grid;
    margin-bottom: 4rem;
}

.Example .Title {
    padding: var(--padding);
    margin: 0;
    /* font-size: minmax(10px, 100px); */
}

.Example img{
    width: 100%;
}

.Example .About {
    padding: var(--padding);
    margin: 0;
}

.Example .Reviews {
    display: flex;
    align-items: center;
    justify-content: center;
    /* flex-wrap: wrap; */
}

@media(max-width: 1000px) {
    .Example .Reviews {
        display: block;
        /* flex-direction: column; */
        /* align-items: center; */
        /* justify-content: center; */
    }
}

/* .Reviews div {
    padding: 2rem;
}

.Reviews span {
    display: block;
    font-style: italic;
} */

.Spec table {
    width: 100%;
}

.Spec td {
    margin: 0;
    padding: 1rem 0;
    border-bottom: 1px solid rgba(128, 128, 128, 0.473);
}

.Spec td:first-child {
    padding-right: 2rem;
    
}

.Spec tr td:first-child {
    font-weight: 600;
}

@media(min-width: 1000px) {
    .Example {
        grid-template-columns: 30rem 1fr;
    }
    .Example .Title {
        grid-column: 2 / 3;
        padding: 0 calc(var(--padding) * 1.5);
    }
    .Example img {
        grid-column: 1 / 2;
    }
    .Example .About {
        padding: 0 calc(var(--padding) * 1.5);
        margin: 0;
    }

    .Example .Reviews {
        grid-column: 1 / -1;
    }
}