body {
  margin: 0;
  padding: 0;
  font-family: 'Fira Code', sans-serif;
  /* font-family: 'Montserrat', sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  line-height: 1.5rem;
  /* letter-spacing: -.5px; */
  word-spacing: 1px;
}

*, *::before, *::after {
  box-sizing: border-box;
}

:root {
  --padding: 2rem;
}

