.App {
  position: relative;
}

.Cover {
  color: white;
  text-align: center;

  background-image: url(./imgs/laptop46454.jpg);
  background-position: center;
  background-color: rgba(0, 0, 0, 0.4);
  background-blend-mode: multiply;
  background-repeat: no-repeat;
  background-size: cover;

  width: 100vw;
  height: 100vh;
  display: grid;
  align-content: center;
}

.Cover h1 {
  font-size: 42px;
  padding: 0;
  margin: 0;
}

.Prolog {
  padding: 2rem;
}

.BackButton {
  /* --color: rgb(115, 241, 231); */
  cursor: pointer;
  --color: rgb(151, 151, 151);
  position: fixed;
  /* display: block; */
  font-family: 'Fira Code';
  color: var(--color);
  font-size: 2rem;
  /* padding: 10px; */

  width: 3.5rem;
  height: 3.5rem;

  /* margin: 1rem; */
  bottom: 1rem;
  right: 1rem;
  border-radius: 100%;
  border: 1px solid var(--color);
  outline: none;
  /* filter: invert(.5); */

  background-color: rgba(255, 255, 255, 0.8);
  opacity: .5;
  z-index: 100;
  /* background-blend-mode: multiply; */
}
.BackButton:hover,
.BackButton:focus {
  opacity: 1;
}

.Kotek {
  position: fixed;
  /* margin-left: auto; */
  /* margin-right: auto; */
  /* align-self: center; */
  /* justify-self: center; */
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Kotek img {
  display: block;
  height: 100%;
}
.Kotek p {
  background-color: white;
  font-size: 30px;
  font-weight: bold;
  padding: 1rem;
  /* width: 100%; */
}

